async function fetchReviewsId(id) {
  const LANGUAGE = `&language=${getLanguage()}`;
  const res = await fetch(
    `${MAIN_URL}movie/${id}/reviews${API_KEY}${LANGUAGE}`,
  );
  if (!res.ok) {
    throw new Error('Network response was not ok');
  }
  return res.json();
}

const fetchApi = {
  page: 1,
  queryString: '',
  param: '',
  fetchTrending, //–ø–æ–ø—É–ª—è—Ä–Ω—ã–µ (1 —Å—Ç—Ä–∞–Ω–∏—Ü–∞)
  fetchSearchMovie, //–ø–æ–∏—Å–∫ –ø–æ –Ω–∞–∑–≤–∞–Ω–∏—è (–Ω—É–∂–Ω–æ –ø–µ—Ä–µ–¥–∞–≤–∞—Ç—å –Ω–∞–∑–≤–∞–Ω–∏–µ)
  fetchMovieByGanres,
  //**—Å–ø–∏—Å–æ–∫ —Ñ–∏–ª—å–º–æ–≤ —Å–æ–≥–ª–∞—Å–Ω–æ –≤—ã–±—Ä–∞–Ω—ã—Ö –∂–∞–Ω—Ä–æ–≤(–∞–π–¥–∏ –∂–∞–Ω—Ä–æ–≤ —á–µ—Ä–µ–∑ –∑–∞–ø—è—Ç—É—é)*/
  getMovieData,

  fetchMovieId, //–ø–æ–ª—É—á–∏—Ç—å —Ñ–∏–ª—å–º –ø–æ id
  fetchCreditsId, //—Å–ø–∏—Å–æ–∫ –∞–∫—Ç–µ—Ä–æ–≤ (–¥–ª—è –ø–æ–ª—É—á–µ–Ω–∏–µ –Ω—É–∂–µ–Ω id —Ñ–∏–ª—å–º–∞)
  fetchReviewsId, //–æ–±–∑–æ—Ä—ã (–¥–ª—è –ø–æ–ª—É—á–µ–Ω–∏–µ –Ω—É–∂–µ–Ω id —Ñ–∏–ª—å–º–∞)
  fetchVideosId, //—Ç—Ä–µ–π–ª–µ—Ä—ã (–¥–ª—è –ø–æ–ª—É—á–µ–Ω–∏–µ –Ω—É–∂–µ–Ω id —Ñ–∏–ª—å–º–∞)
  fetchGanres, //–ø–æ–ª–Ω—ã–π —Å–ø–∏—Å–æ–∫ –∂–∞–Ω—Ä–æ–≤

  reset() {
    this.page = 1;
  },
  incrementPage() {
    this.page += 1;
  },
  setPage(currentPage) {
    this.page = currentPage;
  },
  getPage() {
    return this.page;
  },
  setQueryString(newQueryString) {
    this.queryString = newQueryString;
  },
  getQueryString() {
    return this.queryString;
  },
  getParam() {
    return this.param;
  },
  setParam(param) {
    this.param = param;
  },
  setLocation(curLoc) {
    location.href = curLoc;
    location.hash = curLoc;
  },
};

export default fetchApi;
